.navbar-header {
	height: 60px;
	display: flex;
	align-items: center;
	background-color: #F6F8FA;
	position: sticky;
	top: 0;
	z-index: 100;
	justify-content: space-between;
	text-decoration: none;
}

.new-nav {
	display: flex;
	justify-self: space-evenly;
	text-decoration: none;

}

.header_logo {
	margin: 0 20px;
	margin-top: 0px;
	text-decoration: none;

}

.nav_options {
	display: flex;
	margin-left: 10px;
	margin-right: 10px;
	color: black;
	text-decoration: none;


}

ul {
	list-style-type: none;
}



